.service-cotent{
   padding-left : 5%;
   padding-right : 5%;
}
.our-services-1{
   padding-bottom : 30px;
}
.our-services {
   background-image: linear-gradient(25deg,rgb(242 251 255) 0%,rgba(0,0,0,0.6) 100%)!important;
}
.our-services-1{
  width : 100%;
  height : 20%;
}
.our-services-heading{
    font-size: 44px;
    color : #0d040fe0;
    padding-left : 5%;
    padding-top : 5%;
    font-family : soleil;
    font-weight : 600;
}
.our-services-heading-1{
    font-size: 44px;
    color : #ffffffc9;
    padding : 15px;
    padding-top : 10%;
    position : relative;
    text-align : left;
    background-image: linear-gradient(90deg,rgba(0,0,0,0.6) 35%,rgba(0,0,0,0.3) 100%),url(../img/2-banner.jpg)!important;
    height : 200px;
}
.our-services-cards{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    max-width: 1380px;
    margin: auto;
}
.our-services-card{
    position: relative;
    min-width: 300px;
    max-width: 350px;
    height: 400px;
    margin-top: 20px;
    margin-bottom: 20px;    
    transition: 0.3s;
    cursor: pointer;
    z-index: 1;
}
.our-services-card-heading{
    position: absolute;
    bottom: -100px;
    visibility: hidden;
    color: #FFF;
    font-size: 24px;
    font-weight: 600;
}
.our-services-card-text{
    position: absolute;
    bottom: -100px;
    visibility: hidden;
    color: #FFF;
}
.services-image{
    object-fit : cover;
    object-position: center;
    width: 100%;
    height: 100%;
    border-bottom-left-radius: 20px;
    border-top-right-radius: 20px;
}

.card-overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    visibility: none;
    transition: .5s ease;
    background-color: #393839;
    border-bottom-left-radius: 20px;
    border-top-right-radius: 20px;
}
.our-services-card:hover .card-overlay{
    opacity: 50%;
}
.our-services-card:hover .our-services-card-heading{
    bottom: 40px;
    left: 20px;
    visibility: visible;
    transition: 0.3s;
}
.our-services-card:hover .our-services-card-text{
    bottom: 20px;
    left: 20px;
    visibility: visible;
    transition: 0.3s
}
